// Masthead
//
// Super small header above the content for site name and short description.

.masthead {
  padding-top:    2rem;
  padding-bottom: 1rem;
  margin-bottom: 1rem;
}

.masthead-title {
  margin-top: 0;
  margin-bottom: 0;
  color: #505050;

  a {
    color: #505050;
  }

  small {
    font-size: 50%;
    font-weight: 400;
    color: #c0c0c0;
    letter-spacing: 0;
  }
}

@media (max-width: 480px) {
  h1 {
    font-size: 1.75rem;
  }
  .masthead-title {
    small {
      font-size: 55%;
    }
  }
}
